      if (user) {
        this.user = user;
      } else {
        this.user = null;
      }
    });
  },
  methods: {
    changeColor() {
      if (
        document.body.scrollTop > 200 ||
        document.documentElement.scrollTop > 200
      ) {
        this.bg = "white";
        this.kala = "#93691d";
      } else {
        this.bg = "#0e0e11";
        this.kala = "93691d";
      }
    },

    async share() {
      if (navigator.canShare) {
        navigator.share({
          text: "BREW BROTHERS CAF",
          url: "https://brewbrothercafe.web.app/",
        });
        this.$gtag.event("share-button-click", {
          event_category: "documentation",
          event_label: "Share PWA Button Clicked",
          value: 1,
        });
      }
    },

    async install() {
      this.deferredPrompt.prompt();
      this.$gtag.event("install-pwa-click", {
        event_category: "documentation",
        event_label: "Install PWA Button Clicked",
        value: 1,
      });
